import { Flex, Heading, HStack, Image, Text, useBreakpointValue } from "@chakra-ui/react";
import React from "react";
import { useTranslation } from "react-i18next";

const Description = () => {
    const { t } = useTranslation();
    const isBaseSize = useBreakpointValue(
        { base: true, md: false },
        { ssr: false }
    );

    return (
        <Flex
            w="100%"
            h="100%"
            alignItems="center"
            justifyContent={isBaseSize ? "start" : "space-between"}
            direction={isBaseSize ? "column-reverse" : "row"}
        >
            <Flex
                alignItems={isBaseSize ? "center" : "start"}
                justifyContent="center"
                px={isBaseSize ? 6 : 0}
                direction="column"
                py={isBaseSize ? 8 : 16}
                gridGap={6}
                w="100%"
            >
                <Heading
                    as="h1"
                    size={isBaseSize ? "xl" : "3xl"}
                    fontWeight="bold"
                    textAlign={isBaseSize ? "center" : "left"}
                >
                    {t("appDescription")}
                </Heading>
                <Text
                    fontSize={isBaseSize ? "md" : "2xl"}
                    textAlign={isBaseSize ? "center" : "left"}
                    color="rgba(37, 37, 37, 0.8)"
                >
                    {t("appFeatures")}
                </Text>
            </Flex>
        </Flex>
    );
}

export default Description;
