import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';

i18n
  // detect user language
  // learn more: https://github.com/i18next/i18next-browser-languageDetector
  .use(LanguageDetector)
  // pass the i18n instance to react-i18next.
  .use(initReactI18next)
  // init i18next
  // for all options read: https://www.i18next.com/overview/configuration-options
  .init({
    debug: true,
    fallbackLng: 'en',
    interpolation: {
      escapeValue: false, // not needed for react as it escapes by default
    },
    resources: {
      en: {
            translation: {
                companyName: "NO DNF",
                privacyPolicy: "Privacy Policy",
                allRightsReserved: "Oleksandr Kazimirchuk © {{year}} All rights reserved",
                appDescription: `
                  In the middle of the Ultra Marathon and you are not sure if you are going to make it? No DNF is here to help you!
                  You can plan time spent at refueling stations beforehand and it will calculate your target pace based on your goal time.
                  Have an emergency and need to catch your breath? Do your thing and then input your current distance and the app will re-calculate how much faster you should go or whether it makes sense to go slower and switch to plan B, C, D and etc.
                `,
                appFeatures: "No internet connection (saves battery), no light mode (saves battery), no ads, no subscriptions, no GPS tracking, no data collection, your data is your data!",
                privacyPolicyText: `
                Last updated: 06.02.2024

                No DNF ("we," "our," or "us") is committed to protecting your privacy. This Privacy Policy explains how your personal information is collected, used, and disclosed by No DNF.

                Information Collection and Use

                No DNF is a standalone calculator app designed to perform mathematical operations. The app requires no internet connection and does not collect or use any personal information from its users.

                Information We Don't Collect

                No DNF does not require or collect any of the following information:

                Personal identification information (such as name, email address, etc.)
                Location data
                Data from sensors
                Usage data
                Internet connection data

                All data inputted into No DNF is stored locally on the user's device and can be deleted at any time by the user. We do not have access to any data entered into or generated by the app.

                Data Sharing

                We do not share any data with third parties, as we do not collect, store, or process any personal data.

                Changes to This Privacy Policy

                We may update our Privacy Policy from time to time. We will notify you of any changes by posting the new Privacy Policy on this page. You are advised to review this Privacy Policy periodically for any changes.

                Contact Us

                If you have any questions about this Privacy Policy, please contact us at oleksandr.kazimirchuk.1994+nodnf@gmail.com
                `,
          }
      },
    }
  });

export default i18n;
